import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
let _this,timer;
export default {
	components:{
		uniLoadMore
	},
	data() {
		return {
			record:[],
			loadingType: 'more',
			account: '',
			pageNum: 1,
			pageSize: 20,
			flowType:['動力A'],
			usde: ''
		};
	},
	onLoad:function(options){
		_this = this
		this.usde = options.usde
		// this.account = options.account
		this.fetchList()
	},
	onReachBottom:function(){
		 if (timer != null) {
		            clearTimeout(timer);
		        }
		   timer = setTimeout(function() {
		        _this.fetchMoreList();
		        }, 1000);
	},
	methods:{
		async fetchMoreList(){
			if (this.loadingType ==''){
				return 
			}
			this.pageSize += 20
			let data ={
				pageNum: this.pageNum,
				pageSize: this.pageSize,
				flowType: this.flowType
			}
			const res = await this.$http.get('/user/flow',{data: data})
			if (res.data.data.isLastPage){
				this.loadingType = ''
			}
			this.record = res.data.data.list
			this.record.forEach((item)=>{
				item.createTime = this.handleDate(item.createTime)
				
			})
			console.log(res)
		},
		async fetchList(){
			let data ={
				pageNum: this.pageNum,
				pageSize: this.pageSize,
				flowType: this.flowType
			}
			const res = await this.$http.get('/user/flow',{data: data})
			
			this.record = res.data.data.list
			this.record.forEach((item)=>{
				item.createTime = this.handleDate(item.createTime)
				
			})
			console.log(res)
		},
		handleDate:function(d){
			let arr = d.split(' ')
			let date1 = arr[0].split('-')
			let date2 = arr[1].split(':')
			
			return date1[1]+'-'+date1[2]+' '+date2[0]+':'+date2[1]
		},
	}
}
